<h1 id="using-sequelize-in-node-js" tabindex="-1"><a class="header-anchor" href="#using-sequelize-in-node-js" aria-hidden="true">#</a> Using Sequelize in Node.js</h1>
<p>You can write and send your own queries to your relational database, but what if you later change to another relational database using a little bit different SQL dialect? Then you need to go through all your queries and rewrite some of them ðŸ˜¥ Or, you can use an ORM instead, and let the ORM write the SQL queries for you. Then it's easy to switch to another relational database, and you don't even need to know SQL to use the database ðŸ˜€</p>
<iframe width="560" height="314" src="https://www.youtube.com/embed/0lGWqp-yUTU" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>
<div class="custom-container warning"><p class="custom-container-title">About sequelize.sync()</p>
<p><code>sequelize.sync()</code> will only attempt to create the tables in the database if they don't already exists. If you try to call this method again after you have changed your model definitions, the tables in the database won't be updated to match your new model definitions. Use <code>sequelize.sync({force: true})</code> to make Sequelize first delete your tables in the database, and then re-create them per your new model definitions, or update the tables in the database manually yourself.</p>
</div>
